.products {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 98px;
  padding-top: 121px;

  @media screen and (min-width: 768px) {
    padding-bottom: 112px;
  }
}

.products__list {
  display: block;

  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.products__item {
  position: relative;
  display: block;
  min-width: 280px;
  min-height: 485px;
  border-radius: 24px;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 161px;
  & + & {
    margin-top: 130px;
  }

  @media screen and (min-width: 768px) {
    min-width: 220px;
    padding-top: 152px;
    padding-left: 10px;
    padding-right: 10px;

    & + & {
      margin-left: 20px;
      margin-top: 0;
    }
  }

  @media screen and (min-width: 1280px) {
    & + & {
      margin-left: 30px;
      margin-top: 0;
    }
  }
}

.first-box {
  position: relative;
  background: #ffb8ca;

  @media screen and (min-width: 1280px) {
    min-width: 329px;
    min-height: 525px;
    padding-left: 64.5px;
    padding-right: 64.5px;
    padding-top: 163px;
  }

  &::before {
    content: '';
    position: absolute;
    display: flex;
    width: 249px;
    height: 252px;
    background-image: url('../images/products/ice1-480.png');
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    top: -113px;
    left: 16px;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/products/ice1-480-2x.png');
    }

    @media screen and (min-width: 768px) {
      width: 220px;
      height: 233px;
      left: 0px;
      top: -107px;
      background-image: url('../images/products/ice1-768.png');

      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url('../images/products/ice1-768-2x.png');
      }
    }

    @media screen and (min-width: 1280px) {
      width: 329px;
      height: 335px;
      left: 0px;
      top: -207px;
      background-image: url('../images/products/ice1-1280.png');

      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url('../images/products/ice1-1280-2x.png');
      }
    }
  }
}

