.products__label {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 1.16;
  text-align: center;
  text-transform: uppercase;
  color: $main-color;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }
}

.products__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  margin-bottom: 144px;
  font-size: 34px;
  line-height: 1.14;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $main-header-color;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.14;
    margin-bottom: 150px;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 259px;
    letter-spacing: 0.04em;
  }
}

.products__name {
  position: relative;
  display: block;
  margin-bottom: 66px;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffffff;

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
    letter-spacing: 0.02em;
    margin-bottom: 70px;
  }

  &::after {
    position: absolute;
    content: '';
    top: 53px;
    left: 86px;
    width: 26px;
    height: 6px;
    background-image: url('../images/products/dots.png');
    background-size: 100%;
    background-color: inherit;
    background-repeat: no-repeat;

    @media screen and (min-width: 1280px) {
      top: 66px;
    }
  }
}

.products__slider {
  width: 200px;
  height: 124px;
  overflow: hidden;

  // @media screen and (min-width: 768px) {
  //     width: 198px;
  // }

  // @media screen and (min-width: 1280px) {
  //     width: 241px;
  //     height: 106px;
  // }
}

.slider-line {
  position: relative;
  display: flex;
  width: 400px;
  height: 124px;
  left: 0;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.products__text {
  width: 100%;
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.45;
  text-align: center;
  letter-spacing: 0.04em;
  color: #ffffff;

  // @media screen and (min-width: 768px) {
  //     line-height: 176%;
  //     text-align: center;
  // }

  // @media screen and (min-width: 1280px) {
  //     font-size: 16px;
  //     line-height: 176%;
  // }
}

.products__button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
  padding-left: 5px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #ffffff;
  fill: $main-header-color;
  width: 40px;
  height: 40px;
  &:hover,
  &:focus {
    background-color: $main-header-color;
    fill: #ffffff;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 44px;
  }
}

.products__icon {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
